body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html body{
  
background: rgb(98,125,77); 
background: -moz-linear-gradient(top,  rgba(98,125,77,1) 27%, rgba(98,125,77,1) 27%, rgba(31,59,8,1) 67%); /* FF3.6-15 */
background: -webkit-linear-gradient(top,  rgba(98,125,77,1) 27%,rgba(98,125,77,1) 27%,rgba(31,59,8,1) 67%); /* Chrome10-25,Safari5.1-6 */
background: linear-gradient(to bottom,  rgba(98,125,77,1) 27%,rgba(98,125,77,1) 27%,rgba(31,59,8,1) 67%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#627d4d', endColorstr='#1f3b08',GradientType=0 ); /* IE6-9 */

}
